import React, { PureComponent } from 'react';
import '../css/MainButtons.css';

export class MainButton extends PureComponent {

    render() {
        let btn = <div onClick={this.props.onClick} style={{top: "50%", left: "50%", transform: "translate(-50%, -50%)", position: "absolute", width: "100%"}}>
            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>{this.props.icon}</div>
            <div style={{ display: "flex", alignItems: "center", justifyContent: "center", color: "white" }}>{this.props.title}</div>
        </div>;
        if(this.props.hasOwnProperty('tooltip')){
            try{
                let tooltip = this.props.tooltip;
                tooltip.props.children = btn;
                btn = tooltip;
            }catch (e) {
                console.log(this.props.tooltip, e);
            }
        }
        return btn
    }

}