import React, { PureComponent } from 'react';
import { Tappable, Tabs, TabsItem, Gallery, List, SimpleCell, Avatar } from '@vkontakte/vkui';

let small_slides = [];
export class LeaderBoard extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            slide: 0
        };
    }

    componentDidMount() {
        try{
            const vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0) - 50 - 50 - 27 - 50;
            for(let i = 0; i < this.props.tabs.length; i++){
                let slide = this['slide_' + i];
                const sh = slide.offsetHeight;
                console.log(sh, vh);
                if(sh < vh){
                    small_slides.push(i);
                }
            }
        }catch (e) {
            console.log(e);
        }
    }

    render() {
        let tabs_ = [], slides_ = [], users_ = [];
        try{
            let { tabs, users } = this.props;
            for(let i = 0; i < tabs.length; i++){
                tabs_.push(<TabsItem
                    onClick={() => this.setState({ slide: i })}
                    selected={this.state.slide === i}
                >
                    { tabs[i] }
                </TabsItem>);
                if(!users[i].hasOwnProperty('$$typeof'))
                    for(let l = 0; l < users[i].length; l++){
                        let user = users[i][l];
                        let name_text = user.name.text,
                            name_color = user.name.color,
                            name_icon = user.name.icon;
                        let description_text = user.description.text,
                            description_icon = user.description.icon;
                        let photo = user.photo,
                            link = user.link;
                        users_.push(
                            <Tappable>
                                <div style={{ display: 'flex' }}>
                                    <span style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '39px', fontSize: '20px', fontWeight: '600', letterSpacing: '.5px', color: 'var(--button_primary_background)' }}>
                                        { user.hasOwnProperty('num') ? user.num : (l + 1) }
                                    </span>
                                    <SimpleCell
                                        href={link}
                                        target={'_blank'}
                                        disabled
                                        style={{ width: '100%' }}
                                        before={<Avatar src={photo} />}
                                        description={
                                            description_text && <span style={{ fontSize: '20px', display: 'flex', alignItems: 'center' }}>
                                                {description_text} {description_icon}
                                            </span>
                                        }
                                    >
                                        <span style={{ fontSize: '20px', fontWeight: '600', color: name_color, display: 'flex' }}>
                                            {name_text} { name_icon }
                                        </span>
                                    </SimpleCell>
                                </div>
                            </Tappable>
                        );
                    }
                else
                    users_ = users[i];
                slides_.push(<div ref={el => (this['slide_' + i] = el)} style={{ display: this.state.slide === i ? 'block' : 'none' }}>
                    <List style={{ marginTop: "15px" }}>
                        { users_ }
                    </List>
                </div>);
                users_ = [];
            }
        }catch (e) {

        }

        return (
            <div>
                <Tabs id={'tabs'}>
                    { tabs_ }
                </Tabs>
                { true ?
                    slides_
                    :
                    <div style={{ position: 'absolute', top: '125px', bottom: '0', width: '100%' }}>
                        <Gallery
                            slideWidth="100%"
                            style={{ height: 'auto', paddingBottom: 50 }}
                            slideIndex={this.state.slide}
                            onChange={(slideIndex) => {this.setState({slide: slideIndex});}}>
                            { slides_ }
                        </Gallery>
                    </div>
                }
            </div>
        )
    }

}